.header__container {
  display: none;
  margin: 0 auto;
  padding: 3px 0 31px 0;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .header__container {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
